body {
    background-color: #fff !important;
}

.bgnew {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;

    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    height: 100vh;

    opacity: 0.8;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

.splash-content header {
    color: #fff;
    font-weight: 900;
    font-size: 2.925em;
    margin-bottom: 3%;
    text-shadow: 2px 3px 0px #898999;
}

section {

    padding-bottom: 30px;

    &>div.row,
    &>form>div.row {
        margin-left: 0px;
        margin-right: 0px;
    }

    h1 {
        font-family: 'Montserrat', Arial, Helvetica, sans-serif;
        font-size: 1.6rem;
        font-weight: bold;
        text-transform: uppercase;
        color: #20376b;
        position: relative;

        span {
            font-weight: normal;
        }

        &:after {
            white-space: pre;
            color: #20376b;
            border-left: 2px solid #f1685c;
            font-size: 1rem;
            display: block;
            line-height: 1.3rem;
            padding-left: 10px;
            position: absolute;
            top: 20%;
            right: 0%;
        }

        &.splash-header {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;

            text-transform: none;
            min-width: 500px;

            img {
                width: 164px;
            }

            i {
                font-size: 75px;
            }

            &:after {
                display: none;
            }
        }
    }

    h3 {
        color: #20376b;
        margin-top: 0px;
    }
}

form#fileupload, div#fileupload {
    div.row {
        margin-bottom: 0.8em;
    }
    label {
        display: block;

        small {
            color: #aaa;
            font-weight: normal;
            padding-left: 8px;
        }
    }

    .fileupload-buttonbar {
        .toggle[type='checkbox'] {
            transform: scale(1.2);
            margin-right: 14px;
        }

        button {
            margin: 0px;
        }

        .btn {
            font-weight: normal;
            font-size: 1rem;
            line-height: 1.6rem;
        }
    }

    .table {

        tbody {
            tr {
                border-top: 1px solid #ddd;
                td {
                    border-top-width: 0px;
                }
            }
        }


        .preview-cell{
            width: 90px;
            text-align: center;
        }

        .checkbox-cell {
            width: 40px;
            text-align: center;

            .toggle[type='checkbox'] {
                transform: scale(1.1);
                margin: 0px;
            }
        }

        .name-cell {
            width: 50%;
        }

        .size-cell {
            width: 30%;
        }

        .btn-cell {
            display: flex;
            justify-content: flex-end;
            gap: 6px;

            width: 100px;

            button.btn {
                margin: 0;
                padding: 5px 10px;
                font-size: 1rem;
                font-weight: normal;
                line-height: 1.5;
                border-radius: 3px;

                i {
                    margin-right: 0px;
                }
            }
        }
    }

    input[type=text].form-control,
    textarea.form-control {
        width: 80%;

    }

    .checkbox input[type=checkbox],
    .checkbox-inline input[type=checkbox],
    .radio input[type=radio],
    .radio-inline input[type=radio] {
        margin-top: 7px;
    }

    select.form-control {
        width: 200px;
    }

    textarea.form-control {
        min-height: 50px;
    }

    button i {
        display: inline-block;
        margin-right: 6px;
    }

    li {
        font-size: 1.15em;
    }

    ol ol {
        padding-left: 2em;
        font-size: 1.2em;
    }

    ol ol>li {
        list-style-type: lower-roman;
    }

    p {
        font-size: 0.9em;
        padding: 0.6em;
    }

    button {
        margin: 1em 1em 1em 0;
    }
}

.container_iframe {
    .bg-info {
        background-color: rgba(117, 202, 235, 0.6);

        p {
            color: #333;
        }
    }
}

/*// Drop Zone //////////////////////*/
#dropzone {
    width: 0px;
    height: 0px;
    min-height: 0px;
    line-height: 0px;
    text-align: center;
    font-weight: bold;
    display: grid;
    align-content: center;
    position: absolute;
    left: 0px;
    top: 0px;
    opacity: 0;
    background-color: rgba(245, 251, 249, 0.7);
    color: #848484;

    &.in {
        width: 100%;
        height: 100%;
        font-size: larger;
        border-radius: 0;
        opacity: 1;
    }

    &.fade {
        -webkit-transition: opacity 1s ease-out;
        -moz-transition: opacity 1s ease-out;
        -ms-transition: opacity 1s ease-out;
        -o-transition: opacity 1s ease-out;
        transition: opacity 1s ease-out;

    }
}